  public getAllContacts = () => {
    return this.contacts;
  }
  public getSearchedResult(name, category) {
    let result = [];
    for (let detail of this.contacts) {
      if (detail[category].toLowerCase().search(name.toLowerCase()) != -1) {
        result.push(detail);
      }
    }
    return result;
  }
  public updateDetail(contactDetail) {
    for (let contact of this.contacts) {
      if (contactDetail['E-mail Address'] == contact['E-mail Address']) {
        contact['Display Name'] = contactDetail['Display Name'];
        contact['First Name'] = contactDetail['First Name'];
        contact['Last Name'] = contactDetail['Last Name'];
        contact['Home Phone'] = contactDetail['Home Phone'];
        contact['Notes'] = contactDetail['Notes'];

        return true;
      }

    }
  }
